<script>
import MainNavbar from "@/components/MainNavbar.vue";
import SidebarMain from "@/components/SidebarMain.vue";

export default {
  components: {
    SidebarMain,
    MainNavbar
  },
  data() {
    return {

      formData: {
        dataType: "",
        name: "",
        author: "",
        date: "",
        year: "",
        description_m: "",
        weight: "",
        price: "",
        date_main: "",
        plot: "",
        style: "",
        method: "",
        Colorit: "",
        material: "",
        style_main: "",
        polish: "",
        writing: "",
        status: "",
        stolen_number: "",
        description: "",
        method_of_theft: "",
        image: "",
        author_name: "",
        organization_name: "",
        address: "",
        log_out: "",
        log_out_description: "",
        museum: "",
        museum_worker: "",
        created_at: "",
        updated_at: ""
      },
      myImages1: [],
      files_name1: [],
      imageUrl1: []
    }
  },
  methods: {
    trySubmitFile1(e) {
      this.myImages1 = []
      this.files_name1 = []
      this.myImages1 = []
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name1.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages1.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function (e) {
          vm.imageUrl1.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    getCatalog(){
      this.$http.get('http://api.goskatalog.uz/v1/admin/new-objects?_l=uz&include=image,museum&kp=24287&per-page=10&sort=-count')
    },
    createDate() {

      const form = new FormData();
      form.append("image",this.myImages1[0])
      form.append("dataType", this.formData.dataType);
      form.append("name", this.formData.name);
      form.append("author", this.formData.author);
      form.append("date", this.formData.date);
      form.append("year", this.formData.year);
      form.append("description_m", this.formData.description_m);
      form.append("weight", this.formData.weight);
      form.append("price", this.formData.price);
      form.append("date_main", this.formData.date_main);
      form.append("plot", this.formData.plot);
      form.append("style", this.formData.style);
      form.append("method", this.formData.method);
      form.append("Colorit", this.formData.Colorit);
      form.append("material", this.formData.material);
      form.append("style_main", this.formData.style_main);
      form.append("polish", this.formData.polish);
      form.append("writing", this.formData.writing);
      form.append("status", this.formData.status);
      form.append("stolen_number", this.formData.stolen_number);
      form.append("description", this.formData.description);
      form.append("method_of_theft", this.formData.method_of_theft);
      form.append("author_name", this.formData.author_name);
      form.append("organization_name", this.formData.organization_name);
      form.append("address", this.formData.address);
      form.append("log_out", this.formData.log_out);
      form.append("log_out_description", this.formData.log_out_description);
      form.append("museum", this.formData.museum);
      form.append("museum_worker", this.formData.museum_worker);
      form.append("created_at", this.formData.created_at);
      form.append("updated_at", this.formData.updated_at);
        this.$http.post('/artworks/', form,
          {
            headers: {
              Authorization: `Bearer ${localStorage.getItem('token')}`,
            },
          }
      )
          .then(()=>{
            this.$toast(`Malumot kiritildi`, {
                timeout: 2000,
                type:"success"
              });
          })

          .catch(error => {
            console.log(error.message)
             this.$toast(`${error.message} Malumotlar to'liq kiritilmagan`, {
                timeout: 2000,
                type:"error"
              });

          })
    }
  }
}
</script>